                                </div>
                                <div class="paraText text--primary" :class="$vuetify.breakpoint.smAndDown?'pl-5':'pl-0'">
The Mamallapuram Shore Temple in Tamil Nadu, South India, is a breathtaking example of South Indian architecture that radiates splendor and glory. Believed to have been built during the reign of Pallavan king Rajasimha/Narasimhavarman II, this ancient temple is one of the oldest and most significant temples in the region. Until recently, it was buried under sand, but has now been restored to its former glory.

</div>


<div class="paraText text--primary mt-3" :class="$vuetify.breakpoint.smAndDown?'pl-5':'pl-0'">The temple complex consists of two sanctums dedicated to Lord Shiva and Lord Vishnu, and is built of cut stones and blocks of granite, showcasing the true essence of Dravidian architecture. The pyramidal Kutina-type tower with stepped stories, topped by a cupola and finial, is a remarkable sight to behold. The Shore Temple complex has been designated as a UNESCO World Heritage Site, along with other temples and monuments of Mamallapuram.
  
</div>

<div class="paraText text--primary mt-3" :class="$vuetify.breakpoint.smAndDown?'pl-5':'pl-0'">According to Marco Polo, the Mamallapuram Shore Temple is believed to be one of the Seven Pagodas of Mamallapuram, with six other temples submerged in the sea. The temple is considered to be the last in a series of seven temples built along the coastline. The temple complex is not only a place of historical significance, but also a place of breathtaking beauty, with its intricate carvings, serene surroundings, and rich cultural heritage. A visit to Mahabalipuram is incomplete without experiencing the majestic allure of the Mamallapuram Shore Temple. </div>

